import React from 'react';
import aboutCSS from './about.module.css';
import { Typography } from "@mui/material";
import glassIcon from '../../assets/svg/glasses.svg';

const AboutStore = () => {
  return (
    <div className='componentContainer'>
      <div className={aboutCSS.container}>
        <Typography variant={'h4'} className={aboutCSS.container__title}><strong>  </strong></Typography>
        <Typography variant={'h6'} className={aboutCSS.container__text}> -  -  !  -   : - .       ,       -.       <img src={glassIcon} alt=" " />,   ,       . </Typography>
        <Typography variant={'h6'} className={aboutCSS.container__text}>    -       ,      ,        .         ,      ,           . </Typography>
        <Typography variant={'h6'} className={aboutCSS.container__text}>   ,       .      ,    .       -            ! </Typography>
      </div>
    </div>
  )
}


export default AboutStore;