import React from "react";
import hpCss from './homePage.module.css'
import { Typography } from "@mui/material";
import { NavLink } from "react-router-dom";
import glassIcon from '../../assets/svg/glasses.svg';
import glassFitting from '../../assets/image/img_online_glasess.png';

const HomePage = () => {
    return (
        <div className={`componentContainer ${hpCss.HomePageGradient}`}>
            <div className={hpCss.container__legend}>
                <div className={hpCss.container__legend__text}>
                    <Typography variant={'h3'} className={hpCss.container__title}><strong>   </strong><br />      </Typography>
                    <Typography variant={'h6'} className={hpCss.container__text}> ,   ,         ,    ,    ,     ,           .</Typography>
                    <Typography variant={'h6'} className={hpCss.container__text}> -           .</Typography>
                    <NavLink to='/product' className={hpCss.container__legend__nav}><Typography variant={'h6'} style={{ lineHeight: "18px" }}>   </Typography></NavLink>
                </div>
            </div>
            <div className={hpCss.container__about}>
                <div className={hpCss.container__about__text}>
                    <Typography variant={'h4'} className={hpCss.container__title}><strong>  </strong></Typography>
                    <Typography variant={'h6'} className={hpCss.container__text}> -  -  !  -   : - .       ,       -.       <img src={glassIcon} alt=" " />,   ,       .</Typography>
                    <NavLink to='/about-store' className={hpCss.container__about__nav}><Typography variant={'h6'} className={hpCss.container__about__nav__text}><strong> </strong></Typography></NavLink>
                </div>
                <div className={hpCss.container__about__img}>
                    <NavLink to='/'><img src={glassFitting} alt="" style={{ width: "100%", height: "100%" }} /></NavLink>
                </div>
            </div>
        </div>
    )
}

export default HomePage;
